* {
    box-sizing: border-box;
}
:root {
    --background: rgb(21, 98, 173);
}
.message {
    height: auto;
    width: 400px;
    display: flex;
    /*align-items: center;*/
    margin-top: 10px;
}
.avatar {
    position: relative;
    width: 50px;
    margin-right: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    /*bottom: 0;*/
    /*transform: translateX(60%);*/
}
.img {
    position: absolute;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    /*margin-bottom: 2px;*/
    bottom: 0;
}

.message_content {
    position: relative;
    background-color: var(--background);
    color: rgba(250, 229, 162, 1);
    border-radius: 10px 10px 10px 0;
    border-color: var(--background);
    width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-right: 10px;
}

.message_content:before {
    content: "";
    width: 0px;
    height: 0px;
    position: absolute;
    border-left: 15px solid transparent;
    border-right: 15px solid var(--background);
    border-top: 15px solid transparent;
    border-bottom: 15px solid var(--background);
    left: -16px;
    bottom: 0px;
}

.message_body {
    /*width: 400px;*/
    display: flex;
    flex-direction: column;
    width: 100%;
}

.user_message {
    margin-left: 15px;
    margin-bottom: 10px;
}

.user_name {
    color: rgb(129, 174, 238);
    font-weight: 700;
    padding-bottom: 10px;
    margin-top: 5px;
    margin-left: 15px;
}

.time_container {
    display: flex;
    justify-content: flex-end;
}

.time {
    margin: auto 10px 5px;
    color: #FFFFF0;
    font-weight: 600;
    font-size: 12px;
}